import Vue from 'vue'
import VueRouter from "vue-router";
import App from './App.vue'
import vuetify from './plugins/vuetify'
import Profiles from "@/pages/Profiles";
import TemporossSettings from "@/pages/TemporossSettings";

Vue.config.productionTip = false

const routes = [
  { path:'/', redirect: '/tempoross-settings'},
  { path:'/tempoross-settings', component: TemporossSettings},
  { path:'/profiles', component: Profiles },
]

Vue.use(VueRouter);
Vue.config.errorHandler = function(err, vm, info) {
  console.log(`Error: ${err.toString()}\nInfo: ${info}`);
}

if (process.env.NODE_ENV === 'development') {
  window.javaApp = {
    getProfiles: () => {
      return '[{"name":"test", "lastModified": 1629468546753}, {"name":"Profile 2", "lastModified": 1629327600000}]';
    },
    loadProfile: () => {
      return '[{"name":"test", "lastModified": 1629468546753}, {"name":"Profile 2", "lastModified": 1629327600000}]';
    }
  }
}

const store = Vue.observable({
  profile: {
    temporossSettings: {
      cook: true,
      repair: true,
      putOutFire: true,
      bucketsToCarry: 2,
      world: 422,
      bankItems: true,
    }
  }
})
Vue.prototype.$store = store;

new Vue({
  router: new VueRouter({routes}),
  vuetify,
  render: h => h(App)
}).$mount('#app')
