<template>
  <v-dialog
      v-model="value"
      max-width="290">
    <v-card>
      <v-card-title class="text-h5">
        {{ title }}
      </v-card-title>

      <v-card-text>
        {{ description }}
      </v-card-text>

      <v-card-actions>
        <v-spacer></v-spacer>

        <v-btn
            color="secondary"
            text
            @click="$emit('input', false); $emit('confirm')">
          {{  confirmAction }}
        </v-btn>

        <v-btn
            color="darken-1"
            text
            @click="$emit('input', false)">
          Cancel
        </v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
export default {
  props: {
    value: Boolean,
    title: String,
    description: String,
    confirmAction: { type: String, default: "Confirm" }
  },
}
</script>

