<template>
  <v-card class="pa-3" max-width="400">
    <v-card-text>
      <div class="text-overline mb-4">
        Profiles
      </div>
      <v-list
          subheader
          two-line>
        <template v-for="(profile, index) in profiles">
        <v-list-item :key="profile.lastModified">
          <v-list-item-content>
            <v-list-item-title v-text="profile.name"></v-list-item-title>

            <v-list-item-subtitle v-text="new Date(profile.lastModified).toLocaleDateString()"></v-list-item-subtitle>
          </v-list-item-content>
          <div class="v-list-item__action">
            <v-btn icon @click.prevent="() => loadProfile(profile.name)">
              <v-icon color="grey lighten-1">mdi-download</v-icon>
            </v-btn>
            <v-btn icon @click.prevent="() => showDeleteProfileConfirm(profile.name)">
              <v-icon color="grey lighten-1">mdi-delete</v-icon>
            </v-btn>
          </div>
        </v-list-item>
        <v-divider
            v-if="index < profiles.length - 1"
            :key="index"/>
        </template>
      </v-list>

      <v-form v-model="valid" ref="form">
        <v-text-field
            v-model="newProfileName"
            label="New profile name"
            :rules="nameRules"
            append-outer-icon="mdi-content-save"
            @click:append-outer="saveProfile"
        />
      </v-form>



      <!--  Popups  -->
      <ConfirmDialog
          v-model="confirmDialog"
          title="Delete profile?"
          :description="`Do you wish to delete the profile ${profileName}?`"
          confirm-action="Delete profile"
          @confirm="deleteProfile"/>

      <v-snackbar
          v-model="snackbar"
          timeout="1500"
          color="secondary"
          min-width="1"
          :top="true">
        {{ snackbarText }}
      </v-snackbar>
    </v-card-text>
  </v-card>
</template>

<script>
import ConfirmDialog from "@/components/ConfirmDialog";
export default {
  name: "Profiles",
  components: {ConfirmDialog},
  data() {
    return {
      profiles: this.getProfiles(),
      snackbar: false,
      snackbarText: "",
      confirmDialog: false,
      profileName: "",
      newProfileName: "",
      nameRules: [
        value => !!value || 'Required.',
        value => (value && value.length >= 3) || 'Min 3 characters',
      ],
      valid: false,
    }
  },
  methods: {
    getProfiles() {
      return JSON.parse(javaApp.getProfiles()).sort((first, second) => second.lastModified - first.lastModified);
    },
    loadProfile(profileName) {
      this._showSnackbar(`Profile ${profileName} loaded`);
      this.$store.profile = {
        ...this.$store.profile,
        ...JSON.parse(javaApp.getProfile(profileName))
      };
    },
    showDeleteProfileConfirm(profileName) {
      this.profileName = profileName
      this.confirmDialog = true;
    },
    deleteProfile() {
      this._showSnackbar(`Profile ${this.profileName} deleted`);
      javaApp.deleteProfile(this.profileName);
      this.profiles = this.getProfiles();
    },
    saveProfile() {
      this.$refs.form.validate();
      if (this.valid) {
        javaApp.saveProfile(this.newProfileName, JSON.stringify(this.$store.profile));
        this._showSnackbar(`Profile ${this.newProfileName} saved`);
        this.profiles = this.getProfiles();
        this.$refs.form.reset();
        this.$root.$el.focus();
      }
    },
    _showSnackbar(text) {
      this.snackbarText = text;
      this.snackbar = true;
    }
  }
}
</script>

